@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

.App{
    font-family: 'Lato', sans-serif;
    display: grid;
    grid-template-columns: .3fr 1fr;
    grid-template-rows: .05fr 1fr;
    grid-template-areas: "header header"
                         "usuarios posts";
    row-gap: 1rem;
    column-gap: 1rem;
}

.header{
    text-align: center;
    font-size: 70px;
    font-weight:bolder;
    text-shadow: 3px 3px 8px black;
    grid-area: header;
    background: rgb(176,239,250);
background: linear-gradient(90deg, rgba(176,239,250,1) 22%, rgba(253,154,143,1) 57%);
}
.view-users{
    grid-area: usuarios;
    background-color: #b0effa;
}

.view-posts{
    grid-area: posts;
    background-color: #fd9a8f;
}

.fa-om{
    color: #b0effa;
    text-shadow: 3px 3px 8px white;
}
