.crea-posts{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-content: center;
}
h1{
    text-shadow: -4px 4px 10px #000000,
    4px -4px 10px #ffedec;
}
#formulario-creapost{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-content: center;
    padding: 2rem;
}

.btn-creapost{
    height: 50px;
    margin: 0px 1rem;
    border-radius: 10px;
background: linear-gradient(225deg, #ffa599, #e48b81);
box-shadow:  -8px 8px 16px #d7837a,
             8px -8px 16px #ffb1a4;
    border: none;
    transition: all .2s ease-out;
}

.btn-creapost:active{
    border-radius: 10px;
    color: white;
background: linear-gradient(225deg, #ffa599, #e48b81);
box-shadow:  -5px 5px 10px #d7837a,
             5px -5px 10px #ffb1a4;
}

#form-title{
    border-radius: 10px;
    height: 1.5rem;
    text-align: center;
    background: #e0e0e0;
    box-shadow: inset 5px 5px 10px #bebebe,
                inset -5px -5px 10px #ffffff;
                border: none;
}
#form-author{
    border-radius: 10px;
    height: 1.5rem;
    text-align: center;
    background: #e0e0e0;
    box-shadow: inset 5px 5px 10px #bebebe,
                inset -5px -5px 10px #ffffff;
                border: none;
}
#form-bodypost{
    border-radius: 10px;
    text-align: center;
    background: #e0e0e0;
    box-shadow: inset 5px 5px 10px #bebebe,
                inset -5px -5px 10px #ffffff;
                border: none;
}

input:focus, textarea:focus, button:focus{
    outline: none;
}

