.nuevo-usuario{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

h1{
    text-shadow: -4px 4px 10px #000000,
    4px -4px 10px #dcf4f8;
}

.form-nuevo-usuario{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 0px 2rem;
}

input:focus, textarea:focus, button:focus{
    outline: none;
}

#form-name{
    height: 1.5rem;
    text-align: center;
    margin-bottom: 1rem;
    border-radius: 10px;
    background: #e0e0e0;
    box-shadow: inset 5px 5px 10px #bebebe,
                inset -5px -5px 10px #ffffff;
                border: none;
}

