.btn-creausuario{
    height: 50px;
    margin: 0px 1rem;
    border: none;
    border-radius: 10px;
background: linear-gradient(225deg, #bcffff, #9ed7e1);
box-shadow:  -8px 8px 16px #96cbd5,
             8px -8px 16px #caffff;
    transition: all .2s ease-out;
}

.btn-creausuario:active{
    border-radius: 10px;
    color: white;
background: linear-gradient(225deg, #bcffff, #9ed7e1);
box-shadow:  -5px 5px 10px #96cbd5,
             5px -5px 10px #caffff;
}