@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    font-family: 'Lato', sans-serif;
    display: grid;
    grid-template-columns: .3fr 1fr;
    grid-template-rows: .05fr 1fr;
    grid-template-areas: "header header"
                         "usuarios posts";
    grid-row-gap: 1rem;
    row-gap: 1rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.header{
    text-align: center;
    font-size: 70px;
    font-weight:bolder;
    text-shadow: 3px 3px 8px black;
    grid-area: header;
    background: rgb(176,239,250);
background: linear-gradient(90deg, rgba(176,239,250,1) 22%, rgba(253,154,143,1) 57%);
}
.view-users{
    grid-area: usuarios;
    background-color: #b0effa;
}

.view-posts{
    grid-area: posts;
    background-color: #fd9a8f;
}

.fa-om{
    color: #b0effa;
    text-shadow: 3px 3px 8px white;
}

.crea-posts{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
}
h1{
    text-shadow: -4px 4px 10px #000000,
    4px -4px 10px #ffedec;
}
#formulario-creapost{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
    padding: 2rem;
}

.btn-creapost{
    height: 50px;
    margin: 0px 1rem;
    border-radius: 10px;
background: linear-gradient(225deg, #ffa599, #e48b81);
box-shadow:  -8px 8px 16px #d7837a,
             8px -8px 16px #ffb1a4;
    border: none;
    transition: all .2s ease-out;
}

.btn-creapost:active{
    border-radius: 10px;
    color: white;
background: linear-gradient(225deg, #ffa599, #e48b81);
box-shadow:  -5px 5px 10px #d7837a,
             5px -5px 10px #ffb1a4;
}

#form-title{
    border-radius: 10px;
    height: 1.5rem;
    text-align: center;
    background: #e0e0e0;
    box-shadow: inset 5px 5px 10px #bebebe,
                inset -5px -5px 10px #ffffff;
                border: none;
}
